<script>
import AdIndex from '@/components/Home/Ad_Top.vue'
import Slider from '@/components/Home/Slider.vue'

export default {
  name: 'Index',
  components: {
    AdIndex,
    Slider
  },
  data() {
    return {
      showNumber: 8,
      showNumberOther: 12,
      typeList: [],
      navLeftList: [],
      navRightList: [],
      gameList: [],
      gameList2: [],
      gameList3: [],
      gameListRecent: [],
      gameListNew: [],
      curType: '已汉化',
      curType2: '汉化中',
      count: 20
    }
  },
  created() {
    this.getData()
    this.getNavData()
  },
  mounted() {
  },
  beforeDestroy() {
    this.typeList = []
    this.navLeftList = []
    this.navRightList = []
    this.gameList = []
    this.gameList2 = []
    this.gameList3 = []
    this.gameListRecent = []
    this.gameListNew = []
  },
  methods: {
    getData() {
      this.$http.get('static/data/games.json').then(game => {
        for (var i = 0; i < game.data.length; i++) {
          if (game.data[i].status !== 0) {
            game.data[i].type.forEach(item => {
              if (item === this.curType) {
                // 已汉化
                this.gameList.push(game.data[i])
              } else if (item === this.curType2) {
                // 汉化中
                this.gameList2.push(game.data[i])
              }
            })
          }
        }
        // 其它游戏
        for (var c = 0; c < game.data.length; c++) {
          if (game.data[c].status !== 0) {
            if ((game.data[c].type.indexOf(this.curType) === -1) && (game.data[c].type.indexOf(this.curType2) === -1)) {
              this.gameList3.push(game.data[c])
            }
          }
        }
        // 最近更新
        var rows = game.data
        // 转换日期格式为时间戳
        for (var r = 0, l = rows.length; r < l; r++) {
          var a = rows[r].update
          var b = rows[r].date
          if (a && rows[r].status !== 0) {
            var date1 = Date.parse(new Date(a))
            rows[r].update = date1
            this.gameListRecent.push(rows[r])
          }
          if (b && rows[r].status !== 0) {
            var date2 = Date.parse(new Date(b))
            rows[r].date = date2
            this.gameListNew.push(rows[r])
          }
        }
        // 最近更新排序
        this.gameListRecent.sort(this.compare('update'))
        // 最近发布排序
        this.gameListNew.sort(this.compare('date'))
        // 排序完，还原日期格式
        this.gameListRecent.forEach(el => {
          const date = new Date(el.update)
          const year = date.getFullYear()
          const month = date.getMonth() + 1 > 9 ? date.getMonth() + 1 : '0' + (date.getMonth() + 1)
          const day = date.getDate() + 1 > 9 ? date.getDate() + 1 : '0' + (date.getDate())
          el.update = year + '-' + month + '-' + day
        })
        // 排序-最近发布
        this.gameListNew.forEach(el => {
          const date = new Date(el.date)
          const year = date.getFullYear()
          const month = date.getMonth() + 1 > 9 ? date.getMonth() + 1 : '0' + (date.getMonth() + 1)
          const day = date.getDate() + 1 > 9 ? date.getDate() + 1 : '0' + (date.getDate())
          el.date = year + '-' + month + '-' + day
        })
        this.gameListRecent.reverse()
        this.gameListNew.reverse()
      })
    },
    compare(prop) {
      return function(obj1, obj2) {
        var val1 = obj1[prop]
        var val2 = obj2[prop]
        if (val1 < val2) {
          return -1
        } else if (val1 > val2) {
          return 1
        } else {
          return 0
        }
      }
    },
    getNavData() {
      this.$http.get('static/data/home_nav_ad.json').then(game => {
        // 左侧数据
        for (var i = 0; i < game.data[0].left.length; i++) {
          if (game.data[0].left[i].status === 1) {
            this.navLeftList.push(game.data[0].left[i])
          }
        }
        // 右侧数据
        for (var j = 0; j < game.data[1].right.length; j++) {
          if (game.data[1].right[j].status === 1) {
            this.navRightList.push(game.data[1].right[j])
          }
        }
      })
    }
  }
}

</script>

