<script>
import SupportList from '@/components/Support.vue'
export default {
  name: 'Article',
  components: {
    SupportList
  },
  data() {
    return {
      gameInfo: [],
      gameType: ['primary', 'success', 'default', 'info', 'warning', 'danger', 'add1', 'add2', 'add3', 'add4', 'add5', 'add6'],
      id: this.$route.params.id,
      num: 0,
      vote: ''
    }
  },
  created() {
    this.getData()
  },
  mounted() {},
  methods: {
    getData() {
      this.$http.get('static/data/games.json').then(res => {
        for (var i = 0; i < res.data.length; i++) {
          if (res.data[i].path === this.id) {
            this.gameInfo.push(res.data[i])
            document.title = res.data[i].title + ' - Git游戏 - gityx.com'
            // console.log(this.gameInfo);
            this.num = 1
          }
        }
        if (this.num < 1) {
          //                            console.log(this.num)
          this.$router.push({
            path: '/'
          })
        }
      },
      res => {
        console.log('error')
      }
      )
    },
    open() {
      $('.hide_box').fadeIn()
      $('.shang_box').fadeIn()
    }
  }
}

</script>

