<script>
import SupportList from '@/components/Support.vue'
export default {
  name: 'Demo',
  components: { SupportList },
  data() {
    return {
      userList: []
    }
  },
  created() {
    this.getData()
  },
  mounted() {},
  methods: {
    getData() {
      this.$http.get('static/data/user.json').then(res => {
        if (res.data) {
          var data = []
          res.data.forEach(el => {
            if (el.status !== 0) {
              data.push(el)
            }
          })
          this.userList = data
        }
      },
      res => {
        console.log('error')
      }
      )
    },
    open() {
      $('.hide_box').fadeIn()
      $('.shang_box').fadeIn()
    }
  }
}

</script>

